<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">HOME</el-breadcrumb-item>
      <el-breadcrumb-item>CMP</el-breadcrumb-item>
      <el-breadcrumb-item>CMP Tickets</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="box-card">
      <!-- Search -->
      <el-row :gutter="20">
        <el-col :span="12">
          <el-input
            @clear="getCMPList"
            placeholder="Please Input ..."
            v-model="queryInfo.query"
            clearable
          >
            <el-button @click="getCMPList" slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>

        <el-col :span="4">
          <el-button @click="addCMPVisible = true" type="primary">Add</el-button>
        </el-col>
      </el-row>

      <!--CMP Table -->
      <el-table  stripe :data="CMPList" style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="id" label="ID" width="100"></el-table-column>
        <el-table-column prop="name" label="Name" width="300"></el-table-column>
        <el-table-column prop="description" label="Description"></el-table-column>
        <el-table-column prop="url" label="Url"></el-table-column>
        <el-table-column label="Questionnaire" width="150">
          <template slot-scope="scope">
            <el-button size="mini" @click="showQuestionnaire(scope.row)" icon="el-icon-edit"></el-button>
          </template>
        </el-table-column>
        <el-table-column prop="updateTime" label="Last Updated" width="120"></el-table-column>
      </el-table>

      <!-- Pagination -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!-- EDIT Questionnaire -->
    <el-dialog
      :close-on-click-modal="true"
      title="Questionnaire"
      :visible.sync="editQuestionnaireVisible"
      width="60%"
    >
      <el-button
        v-if="questionnaireInputReadonly===true"
        @click="questionnaireInputReadonly = false"
        type="danger"
        style="margin-bottom: 10px"
        size="mini"
      >UPDATE</el-button>
      <el-button
        v-else
        @click="questionnaireInputReadonly = true"
        style="margin-bottom: 10px"
        size="mini"
      >Undo</el-button>
      <el-input
        :readonly="questionnaireInputReadonly"
        type="textarea"
        :rows="10"
        placeholder="Please input"
        v-model="editQuestionnaire.questionnaire"
      ></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editQuestionnaireVisible = false">Cancel</el-button>
        <el-button type="primary" @click="updateQuestionnaire()">Confirm</el-button>
      </span>
    </el-dialog>

    <!-- Add CMP -->
    <el-dialog title="Add CMP" :visible.sync="addCMPVisible" width="60%">
      <el-form :model="addCMPForm" :rules="rules" status-icon ref="addCMPForm" class="addCMPForm">
        <el-form-item label="Name" prop="name">
          <el-input v-model="addCMPForm.name"></el-input>
        </el-form-item>
        <el-form-item label="URL" prop="url">
          <el-input v-model="addCMPForm.url"></el-input>
        </el-form-item>
        <el-form-item label="Description" prop="description">
          <el-input v-model="addCMPForm.description"></el-input>
        </el-form-item>
        <el-form-item label="Questionnaire" prop="questionnaire">
          <el-input
            type="textarea"
            :rows="10"
            placeholder="Please input"
            v-model="addCMPForm.questionnaire"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="addCMPVisible = false">Cancel</el-button>
        <el-button type="primary" @click="addCMPInfo">Confirm</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      addCMPForm: {
        name: '',
        url: '',
        description: '',
        questionnaire: '',
        userId: ''
      },
      rules: {
        name: [
          {
            required: true,
            message: "Please Input CMP's Name",
            trigger: 'blur'
          },
          { min: 5, message: 'Length should be greater then 5' }
        ],
        url: [
          {
            required: true,
            message: "Please Input CMP's URL",
            trigger: 'blur'
          },
          { min: 5, message: 'Length should be greater then 5' }
        ],
        description: [
          {
            required: true,
            message: "Please Input CMP's Description",
            trigger: 'blur'
          },
          { min: 5, message: 'Length should be greater then 5' }
        ],
        questionnaire: [
          {
            required: true,
            message: "Please Input CMP's Questionnaire",
            trigger: 'blur'
          },
          { min: 10, message: 'Questionnaire should be greater then 5' }
        ]
      },
      editQuestionnaire: {
        id: '',
        questionnaire: ''
      },
      questionnaireInputReadonly: true,
      editQuestionnaireVisible: false,
      addCMPVisible: false,
      currentPage: 1,
      total: 0,
      CMPList: [],
      addDialogVisible: false,
      queryInfo: {
        query: '',
        pageNum: 1,
        pageSize: 5
      }
    }
  },
  created() {
    this.getCMPList()
  },
  methods: {
    async addCMPInfo() {
      this.addCMPForm.userId = window.sessionStorage.getItem('userId')
      const { data: res } = await this.$http.post('addCMP', this.addCMPForm)
      if (res === 'success') {
        this.$message.success('CMP added successfully!')
      } else {
        this.$message.error('failed')
      }
      this.getCMPList()
      this.addCMPVisible = false
    },
    async updateQuestionnaire() {
      if (this.questionnaireInputReadonly === true) {
        this.editQuestionnaireVisible = false
        return null
      }

      const { data: res } = await this.$http.post(
        'updateCMP',
        this.editQuestionnaire
      )
      if (res === 'success') {
        this.$message.success('Questionnaire updated successfully!')
      }
      this.getCMPList()
      this.editQuestionnaireVisible = false
    },
    showQuestionnaire(row) {
      this.editQuestionnaire.questionnaire = row.questionnaire
      this.editQuestionnaire.id = row.id
      this.editQuestionnaireVisible = true
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getCMPList()
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getCMPList()
    },
    async getCMPList() {
      const { data: res } = await this.$http.get('getCMPList', {
        params: this.queryInfo
      })
      console.log(res)
      this.total = res.totalElements
      this.CMPList = res.content
    }
  }
}
</script>

