<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">HOME</el-breadcrumb-item>
      <el-breadcrumb-item>TEAM</el-breadcrumb-item>
      <el-breadcrumb-item>All Team</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="box-card">
      <!-- Search -->
      <el-row :gutter="20">
        <el-col :span="12">
          <el-input
            @clear="getCMPList"
            placeholder="Please Input ..."
            v-model="queryInfo.query"
            clearable
          >
            <el-button @click="getCMPList" slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <!--CMP Table -->
      <el-table stripe :data="CMPList" style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="name" label="Name" width="300"></el-table-column>
        <el-table-column label="Find CMP tickets" width="300">
          <el-button>Search</el-button>
        </el-table-column>
      </el-table>

      <!-- Pagination -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>

    <!-- Questionnaire -->
    <el-dialog title="Questionnaire" :visible.sync="questionnaireVisible" width="60%">
      <span>{{questionnaire}}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="questionnaireVisible = false">Cancel</el-button>
        <el-button type="primary" @click="questionnaireVisible = false">Confirm</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      questionnaire: '',
      questionnaireVisible: false,
      currentPage: 1,
      total: 0,
      CMPList: [],
      addDialogVisible: false,
      queryInfo: {
        query: '',
        pageNum: 1,
        pageSize: 5
      }
    }
  },
  created() {
    this.getCMPList()
  },
  methods: {
    showQuestionnaire(questionnaire) {
      this.questionnaire = questionnaire
      this.questionnaireVisible = true
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getCMPList()
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getCMPList()
    },
    async getCMPList() {
      const { data: res } = await this.$http.get('teams', {
        params: this.queryInfo
      })
      console.log(res)
      this.total = res.totalElements
      this.CMPList = res.content
    }
  }
}
</script>

