<template>
  <div class="login_container">
    <div class="login_box">
      <div class="avatar_box">
        <img src="../assets/logo.png" />
      </div>

      <el-form
        ref="loginFormRef"
        :rules="loginFormRules"
        :model="loginForm"
        label-width="0px"
        class="login_form"
      >
        <!--username-->
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" prefix-icon="el-icon-user"></el-input>
        </el-form-item>
        <!--password-->
        <el-form-item prop="password">
          <el-input v-model="loginForm.password" prefix-icon="el-icon-edit" type="password"></el-input>
        </el-form-item>
        <!--button-->
        <el-form-item class="btns">
          <el-button @click="login" type="primary">Login</el-button>
          <el-button @click="resetLoginForm" type="info">Reset</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loginForm: {
        username: 'admin',
        password: '123456'
      },

      loginFormRules: {
        username: [
          { required: true, message: 'Please Input Username', trigger: 'blur' },
          {
            min: 3,
            max: 5,
            message: 'Length must between: 3-5',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: 'Please Input Password', trigger: 'blur' },
          {
            min: 6,
            max: 10,
            message: 'Length must between: 6-10',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    resetLoginForm() {
      this.$refs.loginFormRef.resetFields()
    },
    login() {
      this.$refs.loginFormRef.validate(async valid => {
        if (!valid) return null
        const { data: res } = await this.$http.post('login', this.loginForm)
        window.sessionStorage.setItem('userId', '9')
        console.log(res)
        // if (res.meta.status !== 200) return this.$message.error('login fail')
        this.$message.success('login success')
        // window.sessionStorage.setItem('token', res.data.token)
        this.$router.push('/home')
      })
    }
  }
}
</script>

<style lang="less" scoped>
.login_container {
  background-color: #2b4b6b;
  height: 100%;
}
.login_box {
  width: 450px;
  height: 300px;
  background-color: #fff;
  border-radius: 3px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.avatar_box {
  height: 130px;
  width: 130px;
  // border: 1px solid #eee;
  border-radius: 50%;
  padding: 10px;
  box-shadow: 0 0 10px #ddd;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  img {
    transform: translate(0, 10%);
    width: 100%;
    height: 75%;
    border-radius: 50%;
    background-color: #fff;
  }
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.btns {
  display: flex;
  justify-content: flex-end;
}
</style>
