<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">HOME</el-breadcrumb-item>
      <el-breadcrumb-item>Package</el-breadcrumb-item>
      <el-breadcrumb-item>All Package</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="box-card">
      <!-- Search -->
      <el-row :gutter="20">
        <el-col :span="12">
          <el-input
            @clear="getCMPPkgList"
            placeholder="Please Input ..."
            v-model="queryInfo.query"
            clearable
          >
            <el-button @click="getCMPPkgList" slot="append" icon="el-icon-search"></el-button>
          </el-input>
        </el-col>

        <el-col :span="4">
          <el-button @click="addCMPVisible = true" type="primary">Add CMP Package</el-button>
        </el-col>
      </el-row>

      <!--Package Table -->
      <el-table stripe :data="CMPPkgList">
        <el-table-column type="index" label></el-table-column>
        <el-table-column type="expand" label="CMP" width="80%">
          <template slot-scope="scope">
            <el-card shadow="always">
              <el-table :data="scope.row.cmplist" width="50%" >
                <el-table-column prop="id" label="id" width="60"></el-table-column>
                <el-table-column prop="name" label="name" width="240"></el-table-column>
                <el-table-column prop="description" label="description"></el-table-column>
                <el-table-column prop="url" label="Url"></el-table-column>
                <el-table-column label="Questionnaire">
                  <template slot-scope="scope">
                    <el-button
                      size="mini"
                      @click="showQuestionnaire(scope.row)"
                      icon="el-icon-edit"
                    ></el-button>
                  </template>
                </el-table-column>
              </el-table>
            </el-card>
          </template>
        </el-table-column>
        <el-table-column prop="name" label="Name" width="300"></el-table-column>
        <el-table-column prop="description" label="Description"></el-table-column>
      </el-table>

      <!-- EDIT Questionnaire -->
      <el-dialog
        :close-on-click-modal="true"
        title="Questionnaire"
        :visible.sync="editQuestionnaireVisible"
        width="60%"
      >
        <el-input
          :readonly="questionnaireInputReadonly"
          type="textarea"
          :rows="10"
          placeholder="Please input"
          v-model="editQuestionnaire.questionnaire"
        ></el-input>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editQuestionnaireVisible = false">Cancel</el-button>
          <el-button type="primary" @click="updateQuestionnaire()">Confirm</el-button>
        </span>
      </el-dialog>

      <!-- Pagination -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>

      <!-- Add CMP Package-->
      <el-dialog
        :close-on-click-modal="false"
        title="Add CMP Package"
        :visible.sync="addCMPVisible"
        width="60%"
      >
        <el-form :model="addCMPForm" :rules="rules" status-icon ref="addCMPForm" class="addCMPForm">
          <el-form-item label="Name" prop="name">
            <el-input v-model="addCMPForm.name"></el-input>
          </el-form-item>
          <el-form-item label="Description" prop="description">
            <el-input v-model="addCMPForm.description"></el-input>
          </el-form-item>

          <el-form-item label="CMP Tickets" prop="cmps">
            <el-input v-model="addCMPForm.cmps"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addCMPVisible = false">Cancel</el-button>
          <el-button type="primary" @click="addCMPPkg">Confirm</el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      addCMPForm: {
        name: '',
        description: '',
        cmps: ''
      },
      rules: {
        name: [
          {
            required: true,
            message: "Please Input CMP's Name",
            trigger: 'blur'
          },
          { min: 5, message: 'Length should be greater then 5' }
        ],
        description: [
          {
            required: true,
            message: "Please Input CMP's Description",
            trigger: 'blur'
          },
          { min: 5, message: 'Length should be greater then 5' }
        ],
        cmps: [
          {
            required: true,
            message: "Please Input CMP's ID, split by ','",
            trigger: 'blur'
          },
          { min: 10, message: 'Length should be greater then 5' }
        ]
      },
      addCMPVisible: false,
      editQuestionnaire: {
        id: '',
        questionnaire: ''
      },
      editQuestionnaireVisible: false,
      questionnaireInputReadonly: true,
      questionnaire: '',
      questionnaireVisible: false,
      currentPage: 1,
      total: 0,
      CMPPkgList: [],
      addDialogVisible: false,
      queryInfo: {
        query: '',
        pageNum: 1,
        pageSize: 5
      }
    }
  },
  created() {
    this.getCMPPkgList()
  },
  methods: {
    async addCMPPkg() {
      this.addCMPVisible = false
      const { data: res } = await this.$http.post(
        'addPackageWithString',
        this.addCMPForm
      )
      this.getCMPPkgList()
      if (res === 'success') {
        this.$message.success('CMP Package added successfully')
      } else {
        this.$message.error('Please input correct CMP ticket ID')
      }
    },
    showQuestionnaire(questionnaire) {
      console.log(questionnaire)
      this.editQuestionnaire.questionnaire = questionnaire.questionnaire
      this.editQuestionnaireVisible = true
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage
      this.getCMPPkgList()
    },
    handleSizeChange(newSize) {
      this.queryInfo.pageSize = newSize
      this.getCMPPkgList()
    },
    async getCMPPkgList() {
      const { data: res } = await this.$http.get('getPackageList', {
        params: this.queryInfo
      })
      console.log(res)
      this.total = res.totalElements
      this.CMPPkgList = res.content
    }
  }
}
</script>

