<template>
  <el-container class="home-container">
    <!-- Header -->
    <el-header>
      <div>
        <img src="../assets/citi.png" height="40px" />
        <span>CMP WIKI</span>
      </div>
      <el-button type="info" @click="logout">logout</el-button>
    </el-header>

    <!-- container -->
    <el-container>
      <!-- Aside -->
      <el-aside :width="isCollapse? '64px' : '200px'">
        <div @click="toggleCollapse" class="toggle-button">|||</div>
        <el-menu
          router
          :collapse="isCollapse"
          :collapse-transition="false"
          :default-active="activePath"
          text-color="#fff"
          background-color="#333744"
          active-text-color="#abcdef"
        >
          <!-- CMP -->
          <el-submenu index="CMPSubmenuIndex">
            <template slot="title">
              <i class="el-icon-s-shop"></i>
              <span>CMP</span>
            </template>
            <el-menu-item index="/cmp" :key="1">
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span>All CMPs</span>
              </template>
            </el-menu-item>
          </el-submenu>

          <!-- User -->
          <el-submenu index="userSubmenuIndex">
            <template slot="title">
              <i class="el-icon-s-custom"></i>
              <span>USER</span>
            </template>
            <el-menu-item index="/user" :key="1">
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span>All Users</span>
              </template>
            </el-menu-item>
          </el-submenu>

          <!-- Team -->
          <el-submenu index="teamSubmenuIndex">
            <template slot="title">
              <i class="el-icon-connection"></i>
              <span>TEAM</span>
            </template>
            <el-menu-item index="/team" :key="1">
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span>All Teams</span>
              </template>
            </el-menu-item>
          </el-submenu>

          <!-- Package -->
          <el-submenu index="packageSubmenuIndex">
            <template slot="title">
              <i class="el-icon-suitcase"></i>
              <span>Package</span>
            </template>
            <el-menu-item index="/package" :key="1">
              <template slot="title">
                <i class="el-icon-menu"></i>
                <span>All Package</span>
              </template>
            </el-menu-item>
          </el-submenu>

        </el-menu>
      </el-aside>
      <!-- container -->
      <el-container>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
    return {
      activePath: '',
      isCollapse: false
    }
  },
  created() {},
  methods: {
    logout() {
      window.localStorage.clear()
      this.$router.push('/login')
    },
    toggleCollapse() {
      this.isCollapse = !this.isCollapse
    }
  }
}
</script>

<style lang="less" scoped>
.home-container {
  height: 100%;
}
.el-header {
  background-color: #373d41;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #fff;
  font-size: 20px;
  > div {
    span {
      margin-left: 15px;
    }
  }
}
.el-aside {
  background-color: #333744;
}
.el-main {
  background-color: #eaedf1;
  border-right: none;
}
.toggle-button {
  background-color: #4a5064;
  font-size: 10px;
  line-height: 24px;
  color: #fff;
  text-align: center;
  letter-spacing: 0.2em;
  cursor: pointer;
}
</style>
